@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);

body {
  margin: 0;
  font-family: 'Viaoda Libre', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Viaoda Libre', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: 'Viaoda Libre', cursive;
}


#top {
    width: 73%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#top-phone {
    display: flex;
    align-items: center;
}

#top-phone ion-icon {
    font-size: 1.3rem;
}

#top-phone p {
    font-size: 1.2rem;
}

#top-phone p {
    margin-top: 15px;
    margin-left: 5px;
}

#top-other ion-icon {
    margin: 0 5px;
    font-size: 1.3rem;
}

#top .hov:hover {
    cursor: pointer;
}

@media screen and (max-width: 1024px) {
    #top {
        width: 95%;
    }
}


#nav {
    background-color: #FFCD58;
}

#logo {
    font-size: 1.5rem;
    color: #000;
    text-decoration: none;
    margin-right: 2rem;
}

#home-link,
#menu-link,
#story-link,
#contact-link {
    color: #000;
    font-size: 1.1rem;
    
    text-transform: uppercase;
}

#home-link:hover,
#menu-link:hover,
#story-link:hover,
#contact-link:hover,
#logo:hover {
    color: #4d4d4d;
    font-weight: bold;
}


#footer {
    background-color: #FFCD58;
}


#reviews-page {
    padding: 2rem;
}

#reviews-page h2 {
    font-size: 2rem;
    margin: 2rem;
}

#reviews-page h4 {
    font-size: 1.50rem;
    display: flex;
    justify-content: flex-end;
}

#reviews-page p {
    font-size: 1.2rem;
    margin-bottom: 0;
}

#reviews {
    width: 80%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
}

.review-post {
    width: 50%;
    padding: 1rem;
}

@media screen and (max-width: 1024px) {
    #reviews {
        width: 80%;
        margin: auto;
        display: block;
    }

    .review-post {
        width: 100%;
    }
}


#head {
    background-image: -webkit-gradinet(linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6))), url(/korlake-restaurant/static/media/home.f5b3148e.jpg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/korlake-restaurant/static/media/home.f5b3148e.jpg);
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    min-width: 100wh;
}

#head h2 {
    padding-top: 15%;
}

#head h1 {
    font-size: 5rem;
}

#top-button {
    background-color: #FFCD58;
    width: 150px;
    height: 40px;
    border: 0;
    font-weight: bold;
    text-transform: uppercase;
    margin: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

#top-button:hover {
    background-color: #ffc233;
}

#top-button:active {
    transition: all 0.5s;
    transform: scale(0.9);
}

#top-reservation-button {
    background-color: transparent;
    border: 1px solid #FFCD58;
    width: 150px;
    height: 40px;
    color: #FFCD58;
    font-weight: bold;
    text-transform: uppercase;
    margin: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

#top-reservation-button:hover {
    background-color: #ffc233;
    color: black;
}

#top-reservation-button:active {
    transition: all 0.5s;
    transform: scale(0.9);
}

#head h2,
#head h1,
#top-button,
#top-reservation-button {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    -o-animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

/* SECOND_SECTION */

#second-section {
    align-items: center;
    margin: 2.3rem auto;
}

#second-section p {
    font-size: 1.3rem;
}

#second-img img {
    width: 50%;
}

#story-button {
    -webkit-text-decoration-color: black;
            text-decoration-color: black;
    text-decoration-thickness: 1px;
}

#story-button h3 {
    color: black;
    font-size: 1.5rem;
}

#story-button h3:hover {
    transition: all 0.5s;
    transform: scale(1.1);
}

#story-button h3:active {
    transition: all 0.5s;
    transform: scale(1);
}

/* MENU-SECTION */

#menu-section {
    background-color: #000;
}

#menu-section h2,
#menu-section h3,
#menu-section p {
    color: #ffffff;
}

#menu-section p {
    font-size: 1.2rem;
}

#arrow-right {
    color: #ffffff;
}

#menu-button {
    -webkit-text-decoration-color: #ffffff;
            text-decoration-color: #ffffff;
    text-decoration-thickness: 1px;
}

#menu-button h3:hover {
    transition: all 0.5s;
    transform: scale(1.1);
}

#menu-button h3:active {
    transition: all 0.5s;
    transform: scale(1);
}

/* FOOD SECTION */

#food-card {
    display: flex;
    flex-wrap: wrap;
}

.food-div {
    width: 25%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.food-div p {
    position: absolute;
    width: 80%;
    color: #fff;
    top: 10%;
    font-size: 2.5rem;
    text-align: center;
}

.food-div img {
    width: 100%;
    height: auto;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

/* MEDIA */

@media screen and (min-width: 2500px) {
    #head h2 {
        padding-top: 17%;
        font-size: 4rem;
    }

    #head h1 {
        font-size: 6rem;
    }

    #top-button {
        width: 250px;
        height: 70px;
        margin-top: 40px;
        font-size: 1.7rem;
    }
}

@media screen and (max-width: 1024px) {
    #head h1 {
        font-size: 2.5rem;
    }

    #head h2 {
        font-size: 2rem;
    }

    #head > div {
        padding-top: 15%;
    }

    #second-section {
        margin-top: 2rem;
        margin-bottom: 4rem;
    }

    #second-img img {
        width: 100%;
    }

    .food-div {
        width: 50%;
    }

    .food-div p {
        font-size: 2rem;
    }
}

@media screen and (max-width: 600px) {
    #head > div {
        padding-top: 50%;
    }
}


#head-title {
    background-color: #262626;
    padding-top: 1%;
    padding-bottom: 1%;
}

#head-title h1 {
    color: white;
    font-size: 3rem;
    padding: 3rem 0;
}

#breakfast {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/breakfast-background.996fa0f5.jpg);
}

#lunch {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/lunch-background.d643cefd.jpg);
}

#dinner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/dinner-background.0eb19e38.jpg);
}

#dessert {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/dessert-background.66a3f9fd.jpg);
}

#breakfast,
#lunch,
#dinner,
#dessert {
    background-size: cover;
    background-position: center;
    min-height: 100vh;
}

.background-product {
    background-color: #FFCD58;
}

.menu-title {
    background-color: #FFCD58;
}

.menu-title h2 {
    margin-bottom: 0;
    font-size: 2.5rem;
}

#background-menu-title {
    background-color: #262626;
}

#menu-reservation-btn {
    background-color: transparent;
    border: 1px solid #FFCD58;
    width: 150px;
    height: 40px;
    color: #FFCD58;
    font-weight: bold;
    text-transform: uppercase;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

#menu-reservation-btn {
    margin-top: 100px;
}

#menu-reservation-btn:hover {
    background-color: #ffc233;
    color: black;
}

#menu-reservation-btn:active {
    transition: all 0.5s;
    transform: scale(0.9);
}


.product {
    padding-top: 60%;
}

.product h3,
.description {
    color: #fff;
}

.product h3 {
    font-size: 2rem;
}

.product p {
    font-size: 1.3rem;
}

.price {
    color: red;
}

@media screen and (max-width: 600px) {
    .product {
        padding-top: 20%;
    }
}


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#reservation-page {
    padding: 3rem 0;
}

form {
    display: flex;
    flex-direction: column;
    margin: auto;
    background-color: #FFCD58;
    padding: 1rem;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

form input,
form textarea {
    border: 0;
    padding: 0.5rem;
    font-family: Arial, Helvetica, sans-serif;
}

form input:focus,
form textarea:focus {
    outline: none;
}

.group {
    display: flex;
    justify-content: space-between;
}

.mini-group {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: 1rem 0;
}

#reservation-page label {
    font-size: 1.3rem;
}

#submit-btn {
    width: 25%;
    margin: 1.5rem auto;
    font-weight: bold;
    text-transform: uppercase;
    background-color: #A4C05B;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

#submit-btn:hover {
    background-color: #56A870;
}

#submit-btn:active {
    transition: all 0.5s;
    transform: scale(0.9);
}

#results h3 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

#results p {
    font-size: 1.3rem;
}

@media screen and (max-width: 600px) {
    #reservation-page {
        width: 90%;
    }

    #submit-btn {
        width: 30%;
    }
}

@media screen and (max-width: 450px) {
    .group {
        display: block;
    }

    .mini-group {
        display: flex;
        flex-direction: column;
        width: 80%;
        margin: 0.5rem auto;
    }

    #comments {
        width: 80%;
        margin: auto;
    }
}


#allstory h1 {
    padding: 5%;
}

#allstory h3 {
    font-size: 2rem;
    margin-bottom: 2rem;
}

#allstory h4 {
    font-size: 2rem;
    margin: 5rem;
}

#food {
    display: flex;
    justify-content: space-around;
    margin-top: 4%;
    flex-wrap: wrap;
}

#food img {
    width: 33.3%;
    -webkit-filter: brightness(75%);
            filter: brightness(75%);
}

#food img:hover {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
}

#header-img {
    width: 100%;
}

#paragraphs p {
    font-size: 1.5rem;
}

#stage {
    width: 50%;
    margin: 5rem 0;
}

@media screen and (max-width: 600px) {

    #stage {
        width: 100%;
        margin: 3rem 0;
    }

    #food {
        margin-top: 3rem;
    }

    #food img {
        width: 100%;
    }
}


#contact-page {
    width: 80%;
    margin: auto;
}

#contact-reservations {
    display: flex;
    justify-content: space-between;
}

#allcontacts {
    width: 50%;
}

#allcontacts h1 {
    padding: 5%;
}

#phone img {
    width: 100%;
    margin-top: 5%;
}

#allcontacts ion-icon {
    font-size: 2rem;
}

#allcontacts p {
    font-size: 1.3rem;
}

#allcontacts .hov:hover {
    cursor: pointer;
}

#reservations-div {
    width: 50%;
    padding: 0 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 1450px) {
    #allcontacts {
        width: 40%;
    }

    #reservations-div {
        width: 60%;
    }
}

@media screen and (max-width: 1200px) {
    #allcontacts {
        width: 30%;
    }

    #reservations-div {
        width: 70%;
    }
}

@media screen and (max-width: 1040px) {
    #contact-reservations {
        display: block;
    }

    #allcontacts {
        width: 100%;
    }

    #reservations-div {
        width: 100%;
    }
}

@media screen and (max-width: 800px) {
    #contact-page {
        width: 100%;
    }

    #reservations-div {
        padding: 0 3rem;
    }
}

@media screen and (max-width: 600px) {
    #reservations-div {
        padding: 0;
    }
}



